.Class {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}

.Class__Image {
  height: 300px;
  background-repeat: no-repeat;
  background-size: cover;
}

.Class__Body {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
}

.Class__Title {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  gap: 2px;
}

.Class__Title__Main {
  font-size: 1.4rem;
  font-weight: bold;
  color: var(--tg-theme-text-color);
}

.Class__Title__Subtitle {
  color: rgba(var(--tg-theme-text-color), 0.44)
}
